import { PRODUCTS } from "../data";
import { useNavigate } from "react-router-dom";

const Products = () => {
  const navigate = useNavigate();

  return (
    <div>
      <div className="page-header">
        <div className="page-header-wrapper container">
          <h2>
            STP team has a proven track record of delivering complex technology
            solutions to clients across the globe. We put a strong focus on
            delivering products that meet the highest standards of quality and
            performance.
          </h2>
        </div>
      </div>
      <div className="products container">
        {PRODUCTS.map(({ project, title, description, image }) => (
          <div key={project} className="product">
            <div className="text">
              <h4>{project}</h4>
              <h2>{title}</h2>
              <p>{description}</p>
            </div>
            <div
              className={`image ${project.toLowerCase()}`}
              role="button"
              onClick={() => {
                if (project === "OTIS") {
                  navigate(`/products/${project}`);
                }
              }}
            >
              <img src={image} alt={project} />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Products;
