import CallToAction from "./shared/CallToAction";

const PackageCard = ({
  package: { title, subtitle, description, items, callToAction },
}) => (
  <div key={title} className="package-card">
    <h4>{title}</h4>
    {subtitle && <h5>{subtitle}</h5>}
    <p className="description">{description}</p>
    {items && (
      <ul>
        {items.map((item) => (
          <li key={item}>{item}</li>
        ))}
      </ul>
    )}
    <div className="call-to-action">
      <CallToAction text={callToAction} to="/contact" />
    </div>
  </div>
);

export default PackageCard;
