import { useState } from "react";
import { useForm } from "@formspree/react";

const ContactForm = () => {
  const [formData, setFormData] = useState({
    name: "",
    company: "",
    email: "",
    message: "",
  });
  const [errors, setErrors] = useState({});
  const [state, handleSubmit] = useForm("xoqbwglb");

  const validateForm = () => {
    const errors = {};
    if (!formData.name) {
      errors.name = "this is a required field";
    }
    if (!formData.company) {
      errors.company = "this is a required field";
    }
    return errors;
  };

  const onSubmit = (e) => {
    e.preventDefault();
    const errors = validateForm();
    if (Object.keys(errors).length > 0) {
      setErrors(errors);
      return;
    }
    console.log(formData);
    handleSubmit(e);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleFocus = (e) => {
    const { name } = e.target;
    const updatedErrors = Object.keys(errors).reduce((acc, cur) => {
      if (cur === name) {
        return acc;
      }
      return {
        ...acc,
        [cur]: errors[cur],
      };
    }, {});
    setErrors(updatedErrors);
  };

  if (state.succeeded) {
    return (
      <p>
        Your message was sent!
        <br />
        Thanks for contacting us!
      </p>
    );
  }

  return (
    <form className="contact-form" onSubmit={onSubmit}>
      <div>
        <input
          type="text"
          name="name"
          placeholder="Name"
          value={formData.name}
          onChange={handleChange}
          onFocus={handleFocus}
        />
        <span className="error-message">{errors.name}</span>
      </div>
      <div>
        <input
          type="text"
          name="company"
          placeholder="Company"
          value={formData.company}
          onChange={handleChange}
          onFocus={handleFocus}
        />
        <span className="error-message">{errors.company}</span>
      </div>
      <div>
        <input
          type="email"
          name="email"
          placeholder="Email"
          value={formData.email}
          onChange={handleChange}
        />
      </div>
      <textarea
        name="message"
        placeholder="How can we help"
        value={formData.message}
        onChange={handleChange}
      />
      <button
        type="submit"
        className="send-message"
        disabled={state.submitting}
      >
        Send message
        <img
          src="/images/arrow-right-white.png"
          alt="send message"
          className="arrow-white"
        />
        <img
          src="/images/arrow-right-black.png"
          alt="send message"
          className="arrow-black"
        />
      </button>
    </form>
  );
};

export default ContactForm;
